import React from 'react';
import logo from '../../../logo.svg';

const AppHeader = () =>
  <header className="App-header">
    <img src={logo} className="App-logo" alt="logo" />
    <h1 className="App-title">Welcome to Hacker News with React</h1>
  </header>

export default AppHeader;



